import React, { useState, useContext } from "react";
import { Question } from "./Qusetion";
import { QuizContext } from "./QuizApp";
import './style.css'

const Quiz = () => {
    const { score, setscore, gamestate, setgamestate } = useContext(QuizContext)
    const [currentquestion, setcurrentquestion] = useState(0);
    const [option, setoption] = useState("")

    const nextquestion = () => {
        if (Question[currentquestion].answer == option) {
            setscore(score + 1)
        }
        //alert(score)
        setcurrentquestion(currentquestion + 1)
    }
    const finishquiz = () => {
        if (Question[currentquestion].answer == option) {
            setscore(score + 1)
        }
        setgamestate("result")
    }
    return (
        <div className="Quiz" style={{
            height: "450px",
            borderRadius: "10px",
            backgroundColor: "turquoise",
            textAlign: "center"
        }}>
            <h2 style={{ color: "blue", padding: "20px" }}>{Question[currentquestion].Question}</h2>
            <div className="options" style={{ display: "flex", flexDirection: "column", padding: "20px 100px 30px 100px" }}>
                <button onClick={() => setoption("A")} style={{ border: "none", padding: "10px", margin: "5px" }}>{Question[currentquestion].optionA}</button>
                <button onClick={() => setoption("B")} style={{ padding: "10px", border: "none", margin: "5px" }}>{Question[currentquestion].optionB}</button>
                <button onClick={() => setoption("C")} style={{ padding: "10px", border: "none", margin: "5px" }}>{Question[currentquestion].optionC}</button>
                <button onClick={() => setoption("D")} style={{ padding: "10px", border: "none", margin: "5px" }}>{Question[currentquestion].optionD}</button>
            </div>
            {currentquestion == Question.length - 1 ? (
                <button onClick={finishquiz} style={{ padding: "10px", border: "none" }}>Finish Quiz</button>
            ) : (
                <button onClick={nextquestion} style={{ padding: "10px", border: "none" }}>Next Question</button>
            )}

        </div>
    )
}
export default Quiz