import React, { useContext } from "react";
import { QuizContext } from "./QuizApp";

const MainMenu = () => {
    const { gamestate, setgamestate, Name, setName } = useContext(QuizContext)
    return (
        <div className="MainMenu" style={{
            height: "400px",
            borderRadius: "10px",
            backgroundColor: "turquoise",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            padding: "50px"
        }}>
            <h4 style={{ color: "blue" }}>Enter your Name </h4>
            <input type="text" placeholder="Ex:Sam" onChange={(event) => { setName(event.target.value) }} style={{ margin: "5px", border: "none", borderRadius: "5px" }} />
            <button onClick={() => { setgamestate("quiz") }} style={{ border: "none", padding: "10px", margin: "20px" }}>Start Quiz</button>
        </div>
    )
}
export default MainMenu